
span {
  top: -5px;
}
@media screen and (max-width: 480px) {
span {
      float: none !important;
      margin: 1.25em auto !important;
}
}
.article {
  font-size: 1.35em;
  line-height: 1.65em;
  margin-bottom: 1em;
}
.article__link {
    font-weight: normal;
    text-decoration: underline;
    cursor: pointer;
    -webkit-transition: color .3s ease;
    transition: color .3s ease;
    color: rgba(255, 255, 255, 0.75);
}
.article__link:hover {
      color: #1cb495;
}



